<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="filters">
        <el-form-item>
          <el-input v-model="filters.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="default" @click="searchRequirements"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="default" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <!---->
    <el-table :data="requirements" highlight-current-row :loading="listLoading" style="width: 100%;">
      <el-table-column prop="id" label="id" v-if="false"></el-table-column>
      <el-table-column type="selection" width="55"></el-table-column>
      <el-table-column type="index" width="60"></el-table-column>
      <el-table-column prop="createdDate" label="" :formatter="formatter" width="140"></el-table-column>
      <el-table-column prop="creator" label="" sortable></el-table-column>
      <el-table-column prop="name" label="" sortable>
        <template slot-scope="scope">
          <span class="link-type">{{ scope.row.name }}</span>
          <el-tag :type="tags[scope.row.priority]['type']">{{ tags[scope.row.priority]['name']}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="type" label="" min-width="100" sortable>
        <template slot-scope="scope">
          <span>{{types[scope.row.type]}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="detail" label="" min-width="180" sortable></el-table-column>
      <el-table-column prop="processingPerson" label="" min-width="80" sortable></el-table-column>
      <el-table-column prop="status" label="" min-width="100" sortable>
        <template slot-scope="scope">
          <el-popover trigger="hover" placement="top">
            <p>: {{status[scope.row.status]['hints']}}</p>
            <div slot="reference" class="name-wrapper">
              <el-tag size="medium">{{status[scope.row.status]['name']}}</el-tag>
            </div>
          </el-popover>
        </template>
      </el-table-column>
      <el-table-column label="" width="150">
        <template slot-scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-dialog title="" :visible.sync="addFormVisible" :close-on-click-modal="false">
      <el-form :model="addForm" label-width="80px" ref="addForm">
        <el-form-item label="" prop="name">
          <el-input v-model="addForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="addForm.type">
            <el-radio class="radio" :label="0">BUG</el-radio>
            <el-radio class="radio" :label="1"></el-radio>
            <el-radio class="radio" :label="2"></el-radio>
            <el-radio class="radio" :label="3"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="addForm.status">
            <el-radio class="radio" :label="0">Open</el-radio>
            <el-radio class="radio" :label="1">In Progress</el-radio>
            <el-radio class="radio" :label="2">Resovled</el-radio>
            <el-radio class="radio" :label="3">Reopened</el-radio>
            <el-radio class="radio" :label="4">Closed</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-rate v-model="addForm.priority" :colors="['#99A9BF', '#F7BA2A', '#FF9900']" :max="5" style="margin-top:8px;" />
        </el-form-item>
        <el-form-item label="">
          <el-checkbox-group v-model="addForm.processingPerson">
            <el-checkbox label="" name="processingPerson"></el-checkbox>
            <el-checkbox label="" name="processingPerson"></el-checkbox>
            <el-checkbox label="" name="processingPerson"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="addForm.detail"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="addFormVisible = false"></el-button>
        <el-button type="primary" @click.native="addSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>

    <!---->
    <el-dialog title="" :visible.sync="editFormVisible" :close-on-click-modal="false">
      <el-form :model="editForm" label-width="80px" ref="editForm">
        <el-form-item label="id" prop="id" v-if="false">
          <el-input v-model="editForm.id" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="name">
          <el-input v-model="editForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="editForm.type">
            <el-radio class="radio" label="0">BUG</el-radio>
            <el-radio class="radio" label="1"></el-radio>
            <el-radio class="radio" label="2"></el-radio>
            <el-radio class="radio" label="3"></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-radio-group v-model="editForm.status">
            <el-radio class="radio" label="0">Open</el-radio>
            <el-radio class="radio" label="1">In Progress</el-radio>
            <el-radio class="radio" label="2">Resovled</el-radio>
            <el-radio class="radio" label="3">Reopened</el-radio>
            <el-radio class="radio" label="4">Closed</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="">
          <el-rate v-model="editForm.priority" :colors="['#99A9BF', '#F7BA2A', '#FF9900']" :max="5" style="margin-top:8px;" />
        </el-form-item>
        <el-form-item label="">
          <el-checkbox-group v-model="editForm.processingPerson">
            <el-checkbox label="" name="processingPerson"></el-checkbox>
            <el-checkbox label="" name="processingPerson"></el-checkbox>
            <el-checkbox label="" name="processingPerson"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="">
          <el-input type="textarea" v-model="editForm.detail"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="editFormVisible = false"></el-button>
        <el-button type="primary" @click.native="editSubmit" :loading="editLoading"></el-button>
      </div>
    </el-dialog>
    <!---->
    <el-col :span="24" class="toolbar">
      <!-- <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button> -->
      <div class="pagination-container">
        <el-pagination v-show="filters.total>0" :current-page="filters.page" :page-sizes=[10,20,30,50] :page-size="filters.pageSize" :total="filters.total" background layout="total, sizes, prev, pager, next, jumper" @current-change="handleCurrentChange" />
      </div>
    </el-col>
  </section>

</template>

<script>
import {
  getRequirements,
  createRequirements,
  editRequirements,
  deleteRequirements
} from '../../api/api'
export default {
  data() {
    return {
      filters: { name: '', page: 1, pageSize: 10, total: 10 },
      tags: {
        1: { name: '', type: '' },
        2: { name: '', type: 'success' },
        3: { name: '', type: 'info' },
        4: { name: '', type: 'warning' },
        5: { name: '', type: 'danger' }
      },
      types: {
        0: 'BUG',
        1: '',
        2: '',
        3: ''
      },
      status: {
        0: { name: 'Open', hints: ',' },
        1: { name: 'In Progress', hints: '' },
        2: {
          name: 'Resovled',
          hints: ''
        },
        3: {
          name: 'Reopened',
          hints: 'ReopendClosed'
        },
        4: { name: 'Closed', hints: '' }
      },

      dialogVisible: false,
      requirements: [],
      addFormVisible: false, // 
      editFormVisible: false, // 
      listLoading: false,
      addLoading: false,
      editLoading: false,
      // 
      addForm: {
        id: 0,
        name: '',
        detail: '',
        type: 0,
        status: 0,
        priority: 1,
        processingPerson: []
      },
      // 
      editForm: {}
    }
  },
  methods: {
    handleCurrentChange(val) {
      this.filters.page = val
      this.getRequire()
    },
    formatter(row, column) {
      return row.createdDate ? row.createdDate.substring(0, 16) : ''
    },
    searchRequirements() {
      this.getRequire()
    },
    addSubmit() {
      this.addFormVisible = false
      const addContent = Object.assign({}, this.addForm)
      addContent.creator = this.$store.getters.name
      addContent.processingPerson = this.addForm.processingPerson.join(',')
      this.addForm.processingPerson = []
      createRequirements(addContent).then(response => {
        this.requirements.push(response.data.data)
      })
      this.getRequire()
    },
    editSubmit() {
      this.$confirm('', '', {}).then(() => {
        this.editLoading = true
        this.editForm.processingPerson = this.editForm.processingPerson.join(
          ','
        )
        editRequirements(this.editForm).then(response => {
          this.editFormVisible = false
          const req = response.data.data
          this.editLoading = false
          this.requirements = this.requirements.map(ele => {
            if (parseInt(ele.id) === req.id) {
              return req
            }
            return ele
          })
        })
      })
    },
    handleAdd() {
      const form = {
        id: 0,
        name: '',
        detail: '',
        type: 0,
        status: 0,
        priority: 1,
        processingPerson: []
      }
      this.addForm = Object.assign({}, form)
      this.addFormVisible = true
    },
    handleEdit(index, row) {
      this.editFormVisible = true
      row.priority = parseInt(row.priority)
      this.editForm = Object.assign({}, row)
      this.editForm.processingPerson = this.editForm.processingPerson.split(
        ','
      )
    },
    handleDel(index, row) {
      console.log(index)
      this.$confirm('', '', { type: 'warning' }).then(
        () => {
          this.listLoading = true
          deleteRequirements(row.id).then(res => {
            this.listLoading = false
            this.requirements.splice(index, 1)
          })
        }
      )
    },
    getRequire() {
      getRequirements(this.filters).then(response => {
        const res = response.data.data
        this.requirements = res.items
        this.filters.total = res._meta.totalCount
        this.filters.page = res._meta.currentPage
        this.filters.pageSize = res._meta.perPage
      })
    }
  },
  mounted() {
    this.getRequire()
  }
}
</script>

<style scoped>
</style>


